import { Form, Button, Container, Row, Col , ProgressBar} from 'react-bootstrap';
import '../styles/form.scss';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate, } from 'react-router-dom'
import { useState } from 'react'
import { addCliente, postUsers } from '../features/datosSlice'
import swal from 'sweetalert'

export default function Formulario3(params) {

    const navigate = useNavigate();
    const dispatch = useDispatch();
    const [values, setValues] = useState({});
    const [error, setErrors] = useState({})
    const mistate = useSelector(state => state.datos.user)


    function onHandleChange(e) {
        setValues({ ...values, [e.target.name]: e.target.value })
        setErrors(validate({ ...values, [e.target.name]: e.target.value }))
    }

    function validate(input) {
        let errors = {};
        if (!input.sitLaboral) {
            errors.sitLaboral = 'sin sitiacion laboral'
        }
        if (input.sitLaboral === 'Empleado' && input.empresa.length === 0) {
            errors.empresa = 'empresa no valido'
        }
        if (input.sitLaboral === 'Empleado' && input.dirEmpresa.length === 0) {
            errors.dirEmpresa = 'direccion  no valido'
        }
        if (input.sitLaboral === 'Empleado' && !/^[1-9]\d{7}$/.test(input.telEmpresa)) {
            errors.telEmpresa = 'numero telefonico invalido'
        }

        if (input.sitLaboral === 'Empleado' && !input.tipoContrato) {
            errors.tipoContrato = 'contrato no valido'
        }

        if (!input.ingresoMensual) {
            errors.ingresoMensual = 'no existe ingreso mensual'
        }
        if (!input.gastoMensual) {
            errors.gastoMensual = 'no existe gasto mensual'
        }
        return errors
    }

    function onHandleSubmit(e) {
        e.preventDefault();

        if (Object.keys(values).length === 0 || Object.keys(error).length !== 0) {
            swal({
                title: "ERROR!",
                text: "verifica los campos.",
                icon: "warning",
                dangerMode: true,
            });
        }
        else {
            dispatch(addCliente(values))
            swal({
                title: "Bien!",
                text: "tu informacion laboral esta completa, Continuemos.",
                icon: "success",
            })
                .then(() => {
                    dispatch(postUsers(mistate))
                    navigate('/4')
                })
        }
    }

    return (
        <Container>
            <Row>
                <Col>
                    <ProgressBar animated now={75} />

                    <Form className='login-box' onSubmit={onHandleSubmit}>
                        <Row>
                            <h2>Informacion Laboral</h2>

                            <Row className="mb-3">
                                <Form.Group as={Col} md className='user-box'>
                                    <Form.Label className={error.sitLaboral ? 'dang' : ''}>Situacion Laboral {error.sitLaboral ? '⚠' : ''}</Form.Label>
                                    <Form.Select className='sel shadow-none' name='sitLaboral' onChange={onHandleChange} >
                                        <option value="">Seleccione</option>
                                        <option value='Empleado' >Empleado</option>
                                        <option value='Independiente' >Independiente</option>
                                        <option value='Pensionado' >Pensionado</option>
                                    </Form.Select>
                                </Form.Group>
                                <Form.Group as={Col} md className='user-box'>
                                    <Form.Label className={error.tiempoLaborado ? 'dang' : ''}>Tiempo Laborado {error.tiempoLaborado ? '⚠' : ''}</Form.Label>
                                    <Form.Select className='sel shadow-none' name='tiempoLaborado' onChange={onHandleChange} >
                                        <option value="">Seleccione</option>
                                        <option value='menos de 1 Año ' > Menos de 1 Año</option>
                                        <option value='entre 1 y 3 Años' > Entre 1 y 3 Años</option>
                                        <option value='mas de 3 Años' > Mas de 3 Años</option>
                                    </Form.Select>
                                </Form.Group>
                            </Row>
                        </Row>
                        {values.sitLaboral === 'Empleado' ?
                            <>

                                <Row className="mb-3 ">
                                    <Form.Group as={Col} md className='user-box'>
                                        <Form.Label className={error.empresa ? 'dang' : ''}>Empresa donde Labora {error.empresa ? '⚠' : ''}</Form.Label>
                                        <Form.Control className='shadow-none' type="text" name='empresa' onChange={onHandleChange} />
                                    </Form.Group>

                                    <Form.Group as={Col} md className='user-box'>
                                        <Form.Label className={error.dirEmpresa ? 'dang' : ''}>Direccion de Empresa {error.dirEmpresa ? '⚠' : ''}</Form.Label>
                                        <Form.Control className='shadow-none' type="text" name='dirEmpresa' onChange={onHandleChange} />
                                    </Form.Group>

                                </Row>
                                <Row className="mb-3 ">
                                    <Form.Group as={Col} md className='user-box'>
                                        <Form.Label className={error.telEmpresa ? 'dang' : ''}>Telefono de la empresa {error.telEmpresa ? '⚠' : ''}</Form.Label>
                                        <Form.Control className='shadow-none' type="number" name='telEmpresa' onChange={onHandleChange} />
                                    </Form.Group>

                                    <Form.Group as={Col} md className='user-box'>
                                        <Form.Label className={error.tipoContrato ? 'dang' : ''}>Tipo de Contrato {error.tipoContrato ? '⚠' : ''}</Form.Label>
                                        <Form.Select className='sel shadow-none' name='tipoContrato' onChange={onHandleChange}>
                                            <option value="">Seleccione</option>
                                            <option value='Prestacion de Servicios'>Prestacion de Servicios</option>
                                            <option value='Fijo' >Fijo</option>
                                            <option value='Indefinido' >Indefinido</option>
                                        </Form.Select>
                                    </Form.Group>
                                </Row>

                            </>

                            : ''}

                        <Row className="mb-3 ">
                            <Form.Group as={Col} md className='user-box'>
                                <Form.Label className={error.ingresoMensual ? 'dang' : ''}>Ingresos Mensuales {error.ingresoMensual ? '⚠' : ''}</Form.Label>
                                <Form.Control className='shadow-none' type="number" name='ingresoMensual' onChange={onHandleChange} />
                            </Form.Group>

                            <Form.Group as={Col} md className='user-box'>
                                <Form.Label className={error.gastoMensual ? 'dang' : ''}>Gastos Mensuales {error.gastoMensual ? '⚠' : ''}</Form.Label>
                                <Form.Control className='shadow-none' type="number" name='gastoMensual' onChange={onHandleChange} />
                            </Form.Group>

                        </Row>

                        <Row>
                            <Button variant="new" type="submit">
                                Siguiente
                            </Button>
                        </Row>
                    </Form>
                </Col>

            </Row>
        </Container>
    );

}