import { Form, Button, Container, Row, Col ,ProgressBar} from 'react-bootstrap';
import '../styles/form.scss';
import { useDispatch , useSelector} from 'react-redux';
import { useNavigate, } from 'react-router-dom'
import { useState } from 'react'
import { addCliente , postUsers } from '../features/datosSlice'
import swal from 'sweetalert'

export default function Formulario4(params) {
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const [values, setValues] = useState({});
    const [error, setErrors] = useState({});

    function onHandleChange(e) {
        setValues({ ...values, [e.target.name]: e.target.value })
        setErrors(validate({ ...values, [e.target.name]: e.target.value }))
    }

    function validate(input) {
        let errors = {};
        if (!input.nombreRefPers) {
            errors.nombreRefPers = 'nombre no valido'
        }
        if (!input.nombreRefFam) {
            errors.nombreRefFam = 'nombre no valido'
        }
        if (!/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(input.correoRefPers)) {
            errors.correoRefPers = 'correo invalido'
        }
        if (!/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(input.correoRefFam)) {
            errors.correoRefFam = 'correo invalido'
        }
        if (!/^\s*[0-9]{1,10}\s*$/.test(input.numRefPers)) {
            errors.numRefPers = "celular invalido"
        }
        if (!/^\s*[0-9]{1,10}\s*$/.test(input.numRefFam)) {
            errors.numRefFam = "celular invalido"
        }
        return errors
    }

    function onHandleSubmit(e) {
        e.preventDefault();

        if (Object.keys(values).length === 0 || Object.keys(error).length !== 0) {
            swal({
                title: "ERROR!",
                text: "verifica los campos.",
                icon: "warning",
                dangerMode: true,
            });
        }
        else {
            dispatch(addCliente(values))

            swal({
                title: "Bien!",
                text: "los datos estan perfectos, Continuemos.",
                icon: "success",
            })
                .then(() => {
                    navigate('/')
                    swal({
                        title: "Tu credito casi esta listo!",
                        text: "Verificaremos los datos que nos diste, te enviaremos una respuesta a tu correo, revisalo constantemente !.",
                        icon: "success",
                    })

                })
        }
    }

    return (
        <Container>
            <Row>
                <Col>
                <ProgressBar animated now={100} />

                    <Form className='login-box' onSubmit={onHandleSubmit}>
                        <Row>
                            <h2>Referencias</h2>

                        </Row>
                        <h3>Personal</h3><br />
                        <Row className="mb-3 ">

                            <Form.Group as={Col} md className='user-box'>
                                <Form.Label className={error.nombreRefPers ? 'dang' : ''}>Nombre Completo {error.nombreRefPers ? '⚠' : ''}</Form.Label>
                                <Form.Control className='shadow-none' type="text" name='nombreRefPers' onChange={onHandleChange} />
                            </Form.Group>

                            <Form.Group as={Col} md className='user-box'>
                                <Form.Label className={error.numRefPers ? 'dang' : ''}>Numero Celular {error.numRefPers ? '⚠' : ''}</Form.Label>
                                <Form.Control className='shadow-none' type="number" name='numRefPers' onChange={onHandleChange} />
                            </Form.Group>
                            <Form.Group as={Col} md className='user-box'>
                                <Form.Label className={error.correoRefPers ? 'dang' : ''}>Correo Electronico {error.correoRefPers ? '⚠' : ''}</Form.Label>
                                <Form.Control className='shadow-none' type="text" name='correoRefPers' onChange={onHandleChange} />
                            </Form.Group>

                        </Row>
                        <h3>Familiar</h3><br />
                        <Row className="mb-3 ">

                            <Form.Group as={Col} md className='user-box'>
                                <Form.Label className={error.nombreRefFam ? 'dang' : ''}>Nombre Completo {error.nombreRefFam ? '⚠' : ''}</Form.Label>
                                <Form.Control className='shadow-none' type="text" name='nombreRefFam' onChange={onHandleChange} />
                            </Form.Group>

                            <Form.Group as={Col} md className='user-box'>
                                <Form.Label className={error.numRefFam ? 'dang' : ''}>Numero Celular {error.numRefFam ? '⚠' : ''}</Form.Label>
                                <Form.Control className='shadow-none' type="number" name='numRefFam' onChange={onHandleChange} />
                            </Form.Group>
                            <Form.Group as={Col} md className='user-box'>
                                <Form.Label className={error.correoRefFam ? 'dang' : ''}>Correo Electronico {error.correoRefFam ? '⚠' : ''}</Form.Label>
                                <Form.Control className='shadow-none' type="text" name='correoRefFam' onChange={onHandleChange} />
                            </Form.Group>

                        </Row>

                        <Row>
                            <Button variant="new" type="submit">
                                Terminar
                            </Button>
                        </Row>
                    </Form>
                </Col>

            </Row>
        </Container>
    );

}