import { Form, Button, Container, Row, Col,ProgressBar } from 'react-bootstrap';
import '../styles/form.scss';
import { useSelector, useDispatch } from 'react-redux';
import { useNavigate, } from 'react-router-dom'
import { useState } from 'react'
import { addCliente } from '../features/datosSlice'
import swal from 'sweetalert'


export default function Formulario2(params) {
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const [values, setValues] = useState({});
    const [error, setErrors] = useState({})
    const mistate = useSelector(state => state.datos.municipios)

    function onHandleChange(e) {
        setValues({ ...values, [e.target.name]: e.target.value })
        setErrors(validate({ ...values, [e.target.name]: e.target.value }))
    }

    console.log(values)
    function validate(input) {
        let errors = {};
        if (input.numHijos < 0 || input.numHijos.length === 0) {

            errors.numHijos = 'numero no valido'
        }
        if (input.numPersonasCargo < 0 || input.numPersonasCargo.length === 0) {
            errors.numPersonasCargo = 'numero no valido'
        }

        if (!input.Genero) {
            errors.Genero = 'no existe genero'
        }
        if (!input.ciudadAct) {
            errors.ciudadAct = 'no hay ciudad actual'
        }
        if (!input.estadoCivil) {
            errors.estadoCivil = 'no es valido el estado del estudio'
        }
        if (!input.estadoEstudio) {
            errors.estadoEstudio = 'no es valido el estado del estudio'
        }
        if (!input.nivelEstudio) {
            errors.nivelEstudio = 'no es valido el nivel de estudio'
        }
        if (!input.tiempoVivienda) {
            errors.tiempoVivienda = 'no es valido tiempo en vivienda'
        }
        if (!input.tipoVivienda) {
            errors.tipoVivienda = 'no es valido tipo de vivienda'
        }
        if (!input.direccion) {
            errors.direccion = 'no es valido direccion'
        }

        return errors
    }
    function onHandleSubmit(e) {
        e.preventDefault();

        if (Object.keys(values).length === 0 || Object.keys(error).length !== 0) {
            swal({
                title: "ERROR!",
                text: "verifica los campos.",
                icon: "warning",
                dangerMode: true,
            });
        }
        else {
            dispatch(addCliente(values))
            swal({
                title: "Bien!",
                text: "tus datos adicionales estan muy bien, Continuemos.",
                icon: "success",
            })
            .then(()=>{
                navigate('/3')
            })
        }
    }


    return (
        <Container>
            <Row>
                <Col>
                <ProgressBar animated now={50} />
                    <Form className='login-box' onSubmit={onHandleSubmit}>
                        <Row>
                            <h2>Informacion Personal</h2>

                        </Row>
                        <Row className="mb-3 ">
                            <Form.Group as={Col} md className='user-box'>
                                <Form.Label className={error.numHijos ? 'dang' : ''}>Numero de hijos {error.numHijos ? '⚠' : ''}</Form.Label>
                                <Form.Control className='shadow-none' type="number" name='numHijos' onChange={onHandleChange} />
                            </Form.Group>

                            <Form.Group as={Col} md className='user-box'>
                                <Form.Label className={error.numPersonasCargo ? 'dang' : ''}>Personas a cargo {error.numPersonasCargo ? '⚠' : ''}</Form.Label>
                                <Form.Control className='shadow-none' type="number" name='numPersonasCargo' onChange={onHandleChange} />
                            </Form.Group>

                        </Row>
                        <Row className="mb-3">
                            <Form.Group as={Col} md className='user-box'>
                                <Form.Label className={error.Genero ? 'dang' : ''}>Genero {error.Genero ? '⚠' : ''}</Form.Label>
                                <Form.Select className='sel shadow-none' name='Genero' onChange={onHandleChange} >
                                    <option value="">Seleccione</option>
                                    <option value='Hombre' > Hombre</option>
                                    <option value='Mujer' > Mujer</option>
                                    <option value='Indefinido' > Prefiero no decirlo</option>
                                </Form.Select>
                            </Form.Group>
                            <Form.Group as={Col} md className='user-box'>
                                <Form.Label className={error.estadoCivil ? 'dang' : ''}>Estado Civil{error.estadoCivil ? '⚠' : ''}</Form.Label>
                                <Form.Select className='sel shadow-none' name='estadoCivil' onChange={onHandleChange} >
                                    <option value="">Seleccione</option>
                                    <option value='Soltero' >Solter@</option>
                                    <option value='Casado' >Casad@</option>
                                    <option value='Union Libre' >Union Libre</option>
                                    <option value='Viudo' >Viud@</option>
                                </Form.Select>
                            </Form.Group>
                        </Row>
                        <Row className="mb-3">
                            <Form.Group as={Col} md className='user-box'>
                                <Form.Label className={error.nivelEstudio ? 'dang' : ''}>Nivel de Estudios {error.nivelEstudio ? '⚠' : ''}</Form.Label>
                                <Form.Select className='sel shadow-none' name='nivelEstudio' onChange={onHandleChange} >
                                    <option value="">Seleccione</option>
                                    <option value="Ninguno">Ninguno</option>
                                    <option value="Basico">Basico</option>
                                    <option value="Bachiller">Bachiller</option>
                                    <option value="Tecnico">Tecnico</option>
                                    <option value="Tecnologo">Tecnologo</option>
                                    <option value="Profesional">Profesional</option>
                                    <option value="Especializado">Especializado</option>
                                    <option value="Doctorado">Doctorado</option>
                                </Form.Select>
                            </Form.Group>
                            <Form.Group as={Col} md className='user-box'>
                                <Form.Label  className={error.estadoEstudio ? 'dang' : ''}>Estado de Estudios {error.estadoEstudio ? '⚠' : ''}</Form.Label>
                                <Form.Select className='sel shadow-none' name='estadoEstudio' onChange={onHandleChange} >
                                    <option value="">Seleccione</option>
                                    <option value="Ninguno">Ninguno</option>
                                    <option value="En Curso">En Curso</option>
                                    <option value="Aplazado">Aplazado</option>
                                    <option value="Finalizado">Finalizado</option>
                                </Form.Select>
                            </Form.Group>
                        </Row>
                        <Row className="mb-4">
                            <Form.Group as={Col} md className='user-box'>
                                <Form.Label  className={error.tipoVivienda ? 'dang' : ''}>Tipo de Vivienda {error.tipoVivienda ? '⚠' : ''}</Form.Label>
                                <Form.Select className='sel shadow-none' name='tipoVivienda' onChange={onHandleChange} >
                                    <option value="">Seleccione</option>
                                    <option value="Renta">Renta</option>
                                    <option value="Propia">Propia</option>
                                    <option value="Familiar">Familiar</option>
                                </Form.Select>
                            </Form.Group>

                            <Form.Group as={Col} md className='user-box'>
                                <Form.Label className={error.tiempoVivienda ? 'dang' : ''}>Tiempo en la Vivienda {error.tiempoVivienda ? '⚠' : ''}</Form.Label>
                                <Form.Select className='sel shadow-none' name='tiempoVivienda' onChange={onHandleChange} >
                                    <option value="">Seleccione</option>
                                    <option value="Menos de 1 año">Menos de 1 año</option>
                                    <option value="De 1 a 3 años">De 1 a 3 años</option>
                                    <option value="Mas de 3 años">Mas de 3 años</option>
                                </Form.Select>
                            </Form.Group>
                        </Row>
                        <Row className="mb-3 ">
                            <Form.Group as={Col} md className='user-box'>
                                <Form.Label  className={error.ciudadAct ? 'dang' : ''}>Ciudad donde Vive {error.ciudadAct? '⚠' : ''}</Form.Label>
                                <Form.Select className='sel shadow-none' name='ciudadAct' onChange={onHandleChange} >
                                    <option value="">Seleccione</option>
                                    {
                                        mistate.map((e) => <option key={e.c_digo_dane_del_municipio}>{e.municipio}</option>)

                                    }

                                </Form.Select>
                            </Form.Group>

                            <Form.Group as={Col} md className='user-box'>
                                <Form.Label  className={error.direccion ? 'dang' : ''}>Direccion {error.direccion ? '⚠' : ''}</Form.Label>
                                <Form.Control className='shadow-none' type="text" name='direccion' onChange={onHandleChange} />
                            </Form.Group>

                        </Row>
                        <Row>
                            <Button variant="new" type="submit">
                                Siguiente
                            </Button>
                        </Row>
                    </Form>
                </Col>

            </Row>
        </Container>
    );

}