import { createSlice } from "@reduxjs/toolkit";
import axios from 'axios';


const initialState = {municipios:[],user:{}}

const reducers = {
    addCliente: (state, action) => {
        return {...state, user:{...state.user,...action.payload}}
      
    },
    addMunicipios:(state,action)=>{
        return {...state,...{municipios:action.payload}}

    }
}

export function getMunicipios() {
    return async (dispatch) => {
        await axios.get(`https://www.datos.gov.co/resource/xdk5-pm3f.json`)
            .then((respuesta) =>
                dispatch(addMunicipios(respuesta.data.sort((a,b)=>{
                    if (a.municipio > b.municipio) {
                        return 1;
                      }
                      if (a.municipio < b.municipio) {
                        return -1;
                      }
                      // a must be equal to b
                      return 0;
                }))))
            .catch((error) => console.log(error))
    }
}
export function postUsers(data) {
    return async function (dispatch) {
        await axios.post(`http://localhost:3001/api/users`, data)
        .then((res)=>{
            console.log(res)
        })
        return Response
    }
}

export const datosSlice = createSlice({
    name: 'datos',
    initialState,
    reducers
})


export const { addCliente,addMunicipios } = datosSlice.actions
export default datosSlice.reducer